.rightbar{
    flex: 3.5;
    }
    
    .rightbarWrapper{
        padding: 20px 20px 0 0;
    }
    
    .birthdayContainer{
        display: flex;
        align-items: center;
    }
    
    .birthdayImg{
        height: 40px;
        width: 40px;
        margin-right: 10px;
    }
    
    .birthdayText{
        font-weight: 300;
        font-size: 15px;
    }
    
    .rightbarAd{
        width: 100%;
        border-radius: 10px;
        margin: 30px 0;
    }
    
    .rightbarTitle{
        margin-bottom: 20px;
    }
    
    .rightbarFriendList{
        padding: 0;
        margin: 0;
        list-style: none;
    }
    
    .rightbarTitle{
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 10px;
    }
    
    righbarProfileImgContainer{
        margin-right: 10px;
        position: relative;
    }
    
    rightbarProfileImg{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        object-fit: cover;
    }
    
    .rightbarInfo{
        margin-bottom: 30px;
    }
    
    .rightbarInfoItem{
        margin-bottom: 10px;
    }
    
    .rightbarInfoKey{
        font-weight: 500;
        margin-right: 15px;
        color: #555;
    }
    
    /* .rightbarInfoKeyValue{ issuuueeeeeeeeeeeeesss
        font-weight: 700;
    } */
    
    .rightbarFollowings{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    
    .rightbarFollowing{
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
        cursor: pointer;
    }
    
    .rightbarFollowingImg{
        width: 100px;
        height: 100px;
        object-fit: cover;
        border-radius: 5px;
    }